input.custom-mat {
  font-size: 14px;
  padding: 10px 25px 4px 0px;
  display: block;
  font: inherit;
  border: none;
  border-bottom: 1px solid #949494;
  width: 100%;
  color: #333333;
}
input.custom-mat:focus {
  outline: none;
}
/* LABEL ======================================= */
label.custom-mat {
  color: #757575;
  font-size: 14px;
  font-weight: normal;
  position: absolute;
  pointer-events: none;
  left: 0;
  top: 10px;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}
/* active state */
input.custom-mat:valid ~ label.custom-mat {
  top: -8px;
  font-size: 11px;
  color: #adadad;
}
input.custom-mat:focus ~ label.custom-mat {
  color: #3f51b5;
  top: -8px;
  font-size: 11px;
}
/* BOTTOM BARS ================================= */
.custom-mat-bar {
  position: relative;
  display: block;
}
.custom-mat-bar:before,
.custom-mat-bar:after {
  content: "";
  height: 1px;
  width: 0;
  bottom: 1px;
  position: absolute;
  background: #3f51b5;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}
.custom-mat-bar:before {
  left: 50%;
}
.custom-mat-bar:after {
  right: 50%;
}
/* active state */
input.custom-mat:focus ~ .custom-mat-bar:before,
input.custom-mat:focus ~ .custom-mat-bar:after {
  width: 50%;
}
/* HIGHLIGHTER ================================== */
.highlight {
  position: absolute;
  height: 60%;
  width: 100px;
  top: 25%;
  left: 0;
  pointer-events: none;
  opacity: 0.5;
}
/* active state */
input.custom-mat:focus ~ .highlight {
  animation: inputHighlighter 0.3s ease;
}
/* ANIMATIONS ================ */
@keyframes inputHighlighter {
  to {
    width: 0;
    background: transparent;
  }
}
